export default {
  name: "WarehouseContent",
  props: {
    warehouse: {
      type: Object,
      default() {
        return {}
      }
    },
  },
  data() {
    return {}
  },
  computed: {
    noAddress() {
      let {warehouseProvince = '', warehouseCity = '', warehouseDistrict = '', warehouseAddress = ''} = this.warehouse;
      return !warehouseProvince && !warehouseCity && !warehouseDistrict && !warehouseAddress;
    }
  },
}



