
import Icon from 'vux/src/components/icon/index.vue'

import RDropdown from 'components/common/RDropdown'

export default {
name: 'RSearch',
props: {
isFill: {//是否为提交页面
type: Boolean,
default: false
},
filterList: {
type: Array,
default() {
return []
}
}
},
data() {
return {
srhInpTx: "",
timer: null,
property: '',
};
},
components: {
Icon,
RDropdown,
},
methods: {
getValue(e) {

setTimeout(() => {
this.srhInpTx = e.target.value;
if (!this.srhInpTx) {
this.$emit("search", {
val: this.srhInpTx,
property: this.property,
});
}
}, 0)
},
searchMat(val) {

if (!val) {
this.$emit('turn-off', false);
return;
}
this.$emit("search", {
val: this.srhInpTx,
property: this.property,
});

document.activeElement.blur();
},
clear() {
this.srhInpTx = "";
this.$emit("search", {
val: this.srhInpTx,
property: this.property,
});
},

popSelected(item) {
this.property = item.value;
},
},
created() {
}
};



