
import BScroll from 'better-scroll'
import Spinner from 'vux/src/components/spinner/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


const PULL_DOWN_REFRESH_HEIGHT = 70;
export default {
name: "RScroll",
components: {
Spinner, LoadMore,
},
props: {

hasNext: {
type: Boolean,
default: false
},
noData: {
type: Boolean,
default: false
},
options: {
type: Object,
default() {
return {}
}
},
},
data() {
return {
bScroll: null,
hasRefresh: false,
refreshDone: false, // 是否刷新完成
pullDownTop: -PULL_DOWN_REFRESH_HEIGHT,
}
},
computed: {
tip() {
let tip = '加载中';
if (!this.hasNext) {
tip = '暂无数据'
}
return tip;
}
},
methods: {

_initScroll() {
let options = {
click: true,
probeType: 1,
...this.options,
};
if (this.options.pullDownRefresh) {
this.hasRefresh = true;
options.pullDownRefresh = {
threshold: 40,
stop: PULL_DOWN_REFRESH_HEIGHT
}
}
if (this.options.pullUpLoad) {
options.pullUpLoad = {
threshold: 20
}
}
this.$nextTick(() => {

this.bScroll = new BScroll(this.$refs.bScroll, options);

this.bScroll.on('pullingUp', () => {
if (!this.hasNext) return;
this.$emit('on-pulling-up');
});

this.bScroll.on('pullingDown', () => {
this.disable();
this.refreshDone = false;
this.$emit('on-pulling-down');
});

this.bScroll.on('scroll', ({x, y}) => {
if (y > 0) {
if (y > PULL_DOWN_REFRESH_HEIGHT) {
this.pullDownTop = 0;
} else {
this.pullDownTop = y - PULL_DOWN_REFRESH_HEIGHT;

}
} else {
this.resetPullDown();
}
})
})
},

refresh() {
this.bScroll && this.bScroll.refresh();
},

enable() {
this.bScroll && this.bScroll.enable();
},

disable() {
this.bScroll && this.bScroll.disable();
},

finishPullDown() {
return new Promise(resolve => {
this.resetPullDown();
this.bScroll.finishPullDown();
this.enable();
this.refresh();
resolve();
})
},

finishPullUp() {
return new Promise(resolve => {
this.bScroll.finishPullUp();
this.refresh();
resolve();
})
},

scrollTo({x = 0, y = 0}) {
this.bScroll.scrollTo(x, y);
},

resetPullDown() {
if (this.pullDownTop !== -PULL_DOWN_REFRESH_HEIGHT) {
this.pullDownTop = -PULL_DOWN_REFRESH_HEIGHT;
}
},
},
created() {
this._initScroll();
},
}



