import tokenService from 'service/tokenService'

export default {
  name: "PersonInfo",
  props: {},
  data() {
    return {
      avatar: '',
      userInfo: {}
    }
  },
  methods: {
    // 获取当前用户信息
    getUser() {
      return tokenService.getUser().then(data => {
        this.userInfo = data;
      })
    },
  },
  created() {
    let {avatar = ''} = JSON.parse(sessionStorage.getItem('ROSE_LOGIN_TOKEN') || '{}');
    this.getUser();
  }
}



