export default {
  name: "contactPart",
  props: {
    //外层div是否有contacts_part
    hasClass : {
      type: Boolean,
      default : true
    },
    contactInfo: {
      type: Object,
      default() {
        return {}
      }
    },
    // 是否展示付款方式
    payment: {
      type: Boolean,
      default: true
    },
    // 是否展示物流条款
    logistics: {
      type: Boolean,
      default: true
    },
    //是否展示有限期
    validUntil : {
      type : Boolean,
      default : false

    }
  },
  data() {
    return {}
  },
  computed:{
    noAddress() {
      let {province = '', city = '', county = '', address = ''} = this.contactInfo;
      return !province && !city && !county && !address;
    }
  },
  created(){
  }
}



