export default {
  name: 'FormCell',
  props:{
    title: [String, Number],    // 标题
    content: [String, Number],  // 内容
    showTopBorder: {                // 是否展示上划线
      type: Boolean,
      default: false
    },
    textRight: {                    // 文字居右
      type: Boolean,
      default: false
    },
    showSymbol: {                   // 展示'￥'符号
      type: Boolean,
      default: false
    }
  }
}



