
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import tokenService from 'service/tokenService'
import myReportService from "service/myReportService";
import loading from "components/common/loading";
import RSearch from 'components/common/RSearch'
import RTimer from 'components/common/RTimer'
import RScroll from 'components/common/RScroll'
import PersonInfo from 'components/common/PersonInfo'

export default {
components: {
Tab,
TabItem,
Divider,
loading,
FormPreview,
CellFormPreview,
Countup,
RSearch,
RTimer,
RScroll,
PersonInfo,
},
data() {
return {
spinner: false,
timeFilter: {},
isReset: false,                   // 是否重置
businessAmount: 0,                // 金额合计
activeIndex: 1,
filterList: [],
serachVal: '',
filterProperty: '',
hasNext: true,
scrollOptions: {
click: true,

pullUpLoad: true,
},
page: 1,
limit: 20,
listData: [],
sort: 'desc',
userInfo: {
userCode : ''
},
sortProperty: 'totalAmount',
};
},
filters: {
numberComma
},
methods: {

getUser() {
return tokenService.getUser().then(data => {
this.userInfo = data;
})
},

goList(){
this.$router.push({
path:'/achievement',
query:{
userCode : this.userInfo.userCode
}
}
)
},

getParams() {
let filter = [
{
property: 'saleOwner',
operator: 'like',					//模糊查询like，精确查询eq
value: this.userInfo.userCode
}
];
let sort = [
{
property: this.sortProperty,
direction: this.sort,
}
];
return {
page: this.page,
limit: this.limit,
sort: JSON.stringify(sort),
...this.timeFilter,
filter: JSON.stringify(filter),
}
},

getList() {
return myReportService.allSaleReport(this.getParams()).then(({total = 0, salesman = [], allTotalAmount = 0}) => {

let performance = salesman.reduce((performance, item) => {
return [...performance, ...item.performance];
}, []);
this.hasNext = total > (this.page - 1) * this.limit + performance.length;
this.businessAmount = allTotalAmount;
this.listData = [...this.listData, ...performance];
this.$nextTick(() => {
this.resetScroll();
})
});
},

resetCondition() {
this.listData = [];
this.page = 1;
this.hasNext = true;
this.$nextTick(() => {
this.$refs.bScroll.scrollTo(0, 0);
this.$refs.bScroll.resetPullDown();
})
},

searchList({val = '', property = ''}) {
this.serachVal = val;
this.filterProperty = property;
this.resetCondition();
this.getList();
},

changeDate(time) {
this.timeFilter = time;
this.resetCondition();
this.getList();
},

resetScroll() {
this.$refs.bScroll.finishPullDown();
this.$refs.bScroll.finishPullUp();
},

onPullingUp() {
this.page++;
this.getList();
},

sortByAmt() {
this.sort = this.sort === 'asc' ? 'desc' : 'asc';
this.resetCondition();
this.getList();
},
},
created() {
this.spinner = true;

this.getUser().then(() => {
this.getList();
});
}
};



