
import numberComma from 'vux/src/tools/number/comma.js'

import {toFixed} from 'plugins/calc'

export default {
name: "MatterItem",
props: {
item: {
type: Object,
default() {
return {}
}
},

isReturnMatter: {
type: Boolean,
default: false
}
},
data() {
return {
amtList: ['标准供货单价', '供货单价', '供货金额', '标准零售单价', '零售单价', '零售金额', '销售单价', '销售金额', '销项税额', '税价合计', '每克销售单价'],
}
},
filters: {
numberComma,
toFixed,
},
methods: {

getMatterDefault(item) {
let url = require('@/assets/wl_default.png');
if (item) {
item.inventoryPic = url;
}
return url
},
},
created() {
}
}



