
import saleRepotService from "service/saleRepotService";
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import querystring from 'vux/src/tools/querystring/index.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'


import {toFixed, accAdd, accMul} from 'plugins/calc'

export default {
components: {
Alert,
Cell,
Group,
Divider,
XButton,
Confirm,
LoadMore,
FormPreview,
CellFormPreview
},
data() {
return {
btnStatus: true,
projectList: [], // 项目类产品展示item
finalList: [], // 最终合计展示的item
projectAmt: "", // 项目类产品合计金额
aClassAmt: "", // A类产品合计金额
bClassAmt: "", // B类产品合计金额
finalAmt: "", // 最终合计
childInfo: "",
listSalesAmt: [],
bClassList: [], // b类产品展示的item
};
},
filters: {
numberComma
},
methods: {

sendOrder() {
if (!this.btnStatus) return;
this.$vux.confirm.show({
title: "温馨提示",
content: "请确认提交数据是否正确？",
confirmText: "我已确认",
cancelText: "再去看看",
onConfirm: () => {
saleRepotService.subAmount(querystring.stringify(this.childInfo)).then(data => {
if (data.success) {
this.$vux.alert.show({
title: "提示",
content: data.message,
onHide: () => {
this.$router.replace("/Home");
localStorage.removeItem("saleReportInfo");
localStorage.removeItem("HELP_BASIC_INFO");
localStorage.removeItem("HELP_FORM_INFO");
localStorage.removeItem("SALE_FORM_INFO");
}
});
}
});
}
});
},

info() {
let projectAmt = 0, // 项目类产品金额
aClassAmt = 0, // A类金额
bClassAmt = 0, // B类金额
list4_num = 0, // 最终合计 数量
totalNum1 = 0, // 最终合计 套数
listSalesAmt = [],
bClassList = [];


let jsonData = JSON.parse(this.childInfo.jsonData);
let {transDetailUncalc, baseinfoExt = {}} = jsonData;
console.log('transDetailUncalc:', transDetailUncalc);
for (let i = 0; i < transDetailUncalc.length; i++) {
let item = transDetailUncalc[i];
switch (item.containerCode) {
case '项目类产品':
if (item.transObjCode) {

this.projectList.push({
label: item.transObjCode,
value: `${item.qty || 0}件/折合${item.num1 || 0}套`
});

list4_num = accAdd(list4_num, Number(item.qty || 0));
totalNum1 = accAdd(totalNum1, Number(item.num1 || 0));

projectAmt = accAdd(projectAmt, item.amount);
}
break;
case 'A':
aClassAmt = accAdd(aClassAmt, item.amount);
break;
case '其他A类':
listSalesAmt.unshift({
label: item.transObjCode,
value: `${item.amount}/折合${item.num1}套`,
});
totalNum1 = accAdd(totalNum1, Number(item.num1 || 0));
break;
case 'B':
if (item.transObjCode === 'B类产品') {
bClassAmt = accAdd(bClassAmt, item.amount);
} else {
totalNum1 = accAdd(totalNum1, item.num1);
bClassList.push({
label: item.transObjCode,
value: `￥${item.amount}/折合${item.num1}套`
});
}
break;
default:
break;
}
}
this.projectAmt = "￥" + projectAmt;
this.aClassAmt = "￥" + aClassAmt;
this.bClassAmt = "￥" + bClassAmt;
this.bClassList = bClassList;
this.finalAmt = "￥" + accAdd(Number(aClassAmt), Number(bClassAmt));
this.finalList = [
{
label: "套数",
value: `${totalNum1}套`
}
];
this.listSalesAmt = [
...listSalesAmt,
{
label: '住宿费',
value: `￥${numberComma(baseinfoExt.double7)}`,
}, {
label: '市内交通费',
value: `￥${numberComma(baseinfoExt.double10)}`,
}, {
label: '长途交通费',
value: `￥${numberComma(baseinfoExt.double11)}`,
}, {
label: '其他费用',
value: `￥${numberComma(baseinfoExt.double8)}`,
}, {
label: '合计',
value: `￥${numberComma(baseinfoExt.double9)}`,
}, {
label: '费用销量比',
value: baseinfoExt.varchar13,
},
]
},

isTwoZero() {
saleRepotService.getModelData().then(res => {
if (res.submitAllow === 1) {
this.btnStatus = true;
}
else {
this.btnStatus = false;
this.$vux.alert.show({
title: "提示",
content: "每日提交截止时间为20:00"
});
}
});
},
},
mounted() {
this.childInfo = JSON.parse(
localStorage.getItem("saleReportInfo")
).saleReportRemark;
this.info();
this.isTwoZero();
}
};



