
import numberComma from 'vux/src/tools/number/comma.js'
import dateFormat from 'vux/src/tools/date/format.js'

import {toFixed} from 'plugins/calc'

export default {
name: "ListItem",
props: {
item: {
type: Object,
default() {
return {}
}
},

noPrice: {
type: Boolean,
default: false
},

noQty: {
type: Boolean,
default: false
},
},
data() {
return {}
},
filters: {
dateFormat,
numberComma,
toFixed,
},
methods: {

getDefaultImg(item) {
let url = require('@/assets/wl_default.png');
if (item) {
item.inventoryPic = url;
}
return url
},
}
}



