
import Icon from 'vux/src/components/icon/index.vue'


export default {
name: "RDropdown",
props: {
list: {
type: Array,
default() {
return []
}
}
},
components: {
Icon,
},
data() {
return {
showDrop: false,
selItem: {},
}
},
watch: {
list: {
handler() {
this.selItem = this.list[0];
this.$emit('on-selected', this.selItem);
},
immediate: true
},
},
methods: {

dropClick() {
this.showDrop = !this.showDrop;
},

dropItemClick(item) {
this.selItem = {...item};
this.showDrop = false;
this.$emit('on-selected', this.selItem);
},
},
created() {
}
}



