
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import myReportService from "service/myReportService";
import {accAdd} from 'plugins/calc'
import loading from "components/common/loading";
import RSearch from 'components/common/RSearch'
import RTimer from 'components/common/RTimer'
import RScroll from 'components/common/RScroll'

export default {
components: {
Tab,
TabItem,
Divider,
loading,
SwiperItem,
FormPreview,
CellFormPreview,
Countup,
RSearch,
RTimer,
RScroll,
},
data() {
return {
remark: "",
spinner: false,
timeFilter: {},
staffRank: [],                    // 业务员销售排名(前三)
isReset: false,                   // 是否重置
businessAmount: 0,                // 金额合计
allSaleReport: [],
allProductReport: [],
activeIndex: 0,
rankList: ['业务员', '产品'],
filterList: [ // 过滤列表
{
name: '姓名',
value: 'saleOwnerN',
}, {
name: '部门',
value: 'HANDLER_UNIT_NAME'
},
],
searchVal: '',
filterProperty: '',
hasNext: true,
scrollOptions: {
click: true,

pullUpLoad: true,
},
page: 1,
limit: 20,
listData: [],
sort: 'desc',
decimals: 1,
sortProperty: 'totalAmount',
showTimer : false,
timeText : '日期筛选'
};
},
filters: {
numberComma
},
methods: {
goList(item) {
this.$router.push({
path: '/achievement',
query: {
userCode: item.saleOwner
}
})
},
tabClick(index) {
if (this.activeIndex === index) {//排序
this.sort = this.sort === 'asc' ? 'desc' : 'asc';
}
else{
this.sort = 'desc';
}

let filterLists = [
[{
name: '姓名',
value: 'saleOwnerN',
}, {
name: '部门',
value: 'HANDLER_UNIT_NAME'
}],
[{
name: '存货名称',
value: 'INVENTORY_NAME',
}]
];
let propertyLists = ['totalAmount', 'productTotalAmount'];
this.searchVal = '';
this.activeIndex = index;
this.filterList = filterLists[this.activeIndex];
this.sortProperty = propertyLists[this.activeIndex];
this.resetCondition();
this.getList();
},

getParams() {
let filter = [];
let sort = [
{
property: this.sortProperty,
direction: this.sort,
}
];
if (this.searchVal) {
filter = [
{

property: this.filterProperty,
operator: 'like',					//模糊查询like，精确查询eq
value: this.searchVal
}
]
}
return {
page: this.page,
limit: this.limit,
sort: JSON.stringify(sort),
...this.timeFilter,
filter: JSON.stringify(filter),
}
},
getReport() {
return myReportService.allSaleReport(this.getParams()).then(({total = 0, salesman = [], allTotalAmount = 0}) => {
this.hasNext = total > (this.page - 1) * this.limit + salesman.length;
let dec = `${allTotalAmount}`.split('.');
if (dec.length > 1) {
this.decimals = dec[1].length;
}
this.businessAmount = allTotalAmount;

this.listData = [...this.listData, ...salesman];
this.$nextTick(() => {
this.resetScroll();
})
});
},
getProduct() {
return myReportService.allProductReport(this.getParams()).then(({total = 0, product = [], allTotalAmount = 0}) => {
this.hasNext = total > (this.page - 1) * this.limit + product.length;
this.businessAmount = allTotalAmount;

this.listData = [...this.listData, ...product];
this.$nextTick(() => {
this.resetScroll();
})
});
},

getList() {
switch (this.activeIndex) {
case 0:
this.getReport();
break;
case 1:
this.getProduct();
break;
default:
break;
}
},

resetCondition() {
switch (this.activeIndex) {
case 0:
this.allSaleReport = [];
break;
case 1:
this.allProductReport = [];
break;
default:
break;
}
this.listData = [];
this.page = 1;
this.hasNext = true;
this.$nextTick(() => {
this.$refs.bScroll.scrollTo(0, 0);
this.$refs.bScroll.resetPullDown();
})
},

searchList({val = '', property = ''}) {
this.searchVal = val;
this.filterProperty = property;
this.resetCondition();
this.getList();
},

changeDate(time) {
this.timeText = `${time.startDate}~${time.endDate}`
this.timeFilter = time;
this.resetCondition();
this.getList();
},

resetScroll() {
this.$nextTick(() => {
this.$refs.bScroll.finishPullDown();
this.$refs.bScroll.finishPullUp();
})
},

onPullingUp() {
this.page++;
this.getList();
},

sortByAmt() {
this.sort = this.sort === 'asc' ? 'desc' : 'asc';
this.resetCondition();
this.getList();
},
},
created() {
this.spinner = true;
this.getList();
}
};



